import React, { Component } from 'react';
import '../Css/nav.css';
import { Link } from 'react-router-dom'

class Nav extends Component{

  constructor(props){
    super(props);
  }

  render(){
    return (
      <div className="navbar">
          <Link to="/home">
            <img src="/assets/images/primary_logo.png" alt=""/>
          </Link>
          <Link to={this.props.needed.navlink}>
            <button>{this.props.needed.linkContent}</button>
          </Link>
      </div>
    );
  }
}

export default Nav;
